*   Game Page   *
*****************/

#listTag{
  font-size: 25px;
}
.game-container {
  padding: 2em;
}

.canvas-container {
  position: relative;
  border-radius: 50%;
}

.audio-canvas {
  border-radius: 50%;
  background-color: aliceblue;
}

.play-button, .canvas-container button {
  display: none;
  width: 180px;
  height: 180px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.canvas-container:hover .play-button {
  display: block;
  width: 180px;
  height: 180px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: transparent;
  padding: 0;
}

