* {
  font-family: "Helvetica", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, html {
  height: 100%;
  overflow: auto;
}

/***************
*   General   *
***************/
.App {
  text-align: center;
}

.page-title {
  text-align: center;
  margin: 0 auto;
  font-size: 3em;
  font-weight: 400;
  color: white;
  text-shadow: 1px 1px 2px #000000;
  padding-bottom: .5em;
  padding-top: .75em;
}

.game-menu {
  text-align: center;
  margin: 0 auto;
  font-size: 1.5em;
  color: white;
  text-shadow: 1px 1px 2px #000000;
  padding: 1em;
}

.loading-container {
  position: absolute;
  margin: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.home-container {
  padding: 3em;
  line-height: 1.25em;
}

.faq-container {
  text-align: left;
  padding: 1em 3em 3em 3em;
}

.contact-container {
  padding: 3em;
}

.privacy-container{
  display: block!important;
  padding: 3em;
  min-height: 480px;
  min-width: 70vw;
}

@media screen and (min-width: 900px){
  .privacy-container{
    padding: 3em;
    min-height: 220px;
    min-width: 875px;
  }
}

.contact-container a {
  font-size: 1.25em;
}

.privacy-container p {
  overflow: auto;
  text-align: left;
  line-height: 1.25em;
  font-size: 0.9em;
}

.footer {
  float: right;
  font-size: 1.0em;
}

a:link {
  text-decoration: none;
  color: white;
}

a:link:hover {
  text-decoration: none;
}

a:visited {
  color: white;
}

.privacy-in-dialog {
  overflow: auto;
  padding: 0 0!important;
  font-size: 0.9em;
}

.privacy-corner {
  position: fixed;
  bottom: 6px;
  right: 12px;
  color: white;
  font-size: 1.1em;
}

