.gray {
  color: gray;
}

.pink {
  color: pink;
}

.rule-list {
  margin: auto;
}

.rule-list li {
  margin: 0.4em 0;
}

.example-tags-list {
  margin: 0.65em 0;
  font-size: 0.65em;
}

.example-tags-list li {
  margin: 0.2em 0;
}

/********************
*   Summary Page   *
*******************/
.summary-container {
  color: white;
  padding: 1em 0em;
}

.summary-container h2 {
  align-self: flex-start;
  padding: 0em 2em;
}

.card-list-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  overflow: hidden;
  width: 100%;
  padding: 1em 3em;
}

.card-list {
  flex-wrap: nowrap !important;
  overflow-y: auto;
  transform: translateZ(0);
}

.audio-card {
  text-align: left;
  width: 10em;
  word-break: break-word;
  max-height: 15em;
  overflow-x: scroll;
}

.audio-card h5 {
  margin: 0;
  font-size: 0.75em;
}

.audio-card p {
  font-size: 0.8em;
}

